// <Desktop-specific layout - secondary navigation never present
main > .documentation {
    .content-wrapper {
        @include media('>=small') {
            grid-column: 6/-6;
        }
    }
}

// Generic styles
.documentation {
    .content {
        margin: $measure--small2 0;
    }
}

.slider.doc-slider {
    list-style: none;
    margin: 0 auto;

    li {
        height: $measure--large2;
        width: 100%;
        border: $border-width-default $border-style-solid $color-nearblack;
    }
}
